.contacts {
  padding-top: 40px;
  padding-bottom: 70px;
  background-color: var(--gray-color);

  &__title {
    margin: 0;
    margin-bottom: 45px;
    text-align: center;
    font-family: Kudryashev Headline;
    font-size: 80px;
    font-weight: 400;
    text-transform: uppercase;
  }

  &__content {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 30px;
    height: 450px;
  }

  &__map {
    position: relative;
    grid-column: 12 span;
    border-radius: 20px;
    overflow: hidden
  }

  &__location {
    @include flex-h-center;
    border-radius: 20px;

    position: absolute;
    flex-direction: column;
    padding: 45px 126px 45px 106px;
    top: 0;
    left: 0;
    width: 48.3%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.90);
    z-index: 1;
  }

  &__svg {
    margin-bottom: 35px;
  }

  &__adress {
    margin-bottom: 19px;
    padding-left: 27px;
  }

  &__time {
    margin-bottom: 2px;
    color: var(--shamp-color);
    width: 70%;
    font-size: 16px;
    font-weight: 400;

    &--last {
      margin-bottom: 19px;
    }
  }

  &__phone {
    &-list {
      display: flex;
      flex-direction: column;
      margin-bottom: 19px;
    }

    &-item:not(:last-child) {
      margin-bottom: 10px;
    }

    &-item .phone-list__svg {
      margin-right: 4px;
    }
  }

  &__close-btn {
    position: absolute;
    top: 18px;
    right: 20px;
    outline: none;

    &:focus-visible svg circle {
      fill: var(--tuscany-color);
      transition: fill .3s ease-in-out;
    }

    &:focus-visible svg rect {
      fill: var(--dark-color);
      transition: fill .3s ease-in-out;
    }

    &:hover:not(:focus-visible) svg circle {
      fill: var(--light-color);
      transition: fill .3s ease-in-out;
    }

    &:hover:not(:focus-visible) {
      fill: var(--dark-color);
      transition: fill .3s ease-in-out;
    }

    &:active:not(:focus-visible) svg circle {
      fill: var(--grenadier-color);
      transition: fill .3s ease-in-out;
    }

    &:active:not(:focus-visible) svg rect {
      fill: var(--dark-color);
      transition: fill .3s ease-in-out;
    }
  }
}
