.documents {
  padding-bottom: 50px;
  background-color: var(--gray-color);

  &__container {
    display: flex;
    flex-direction: row-reverse;
  }

  &__title {
    margin: 0;
margin-left: 23px;
    writing-mode: vertical-rl;
    text-align: center;
    font-family: Kudryashev Headline;
    font-size: 80px;
    font-weight: 500;
    line-height: 77px;
    text-transform: uppercase;
    color: #363636;
  }

  &__img {
    // &-list {

    // }

    &-item:not(:last-child) {
      margin-bottom: 20px;
    }
  }

  &__text {

    &-list {
      align-self: center;
    }

    &-item {
      padding-right: 20px;
    }

    &-item:not(:last-child) {
      margin-bottom: 50px;
    }
  }

  &__text {
    margin: 0;
    font-size: 24px;
    font-weight: 400;
    color: var(--dark-color);
  }
}
