.request {
  padding-top: 50px;
  padding-bottom: 40px;
  background-color: var(--gray-color);


  &__container {
    padding-top: 50px;
    padding-bottom: 32px;
    padding-left: 100px;
    padding-right: 100px;
    border-radius: 20px;
    // border: 1px solid var(--shamp-color);
    border: 2px solid var(--gold-color);
    background-color: #E8E5E0; //delete
  }


  &__title {
    margin: 0;
    margin-bottom: 24px;
    font-family: Kudryashev Headline;
    font-size: 64px;
    font-weight: 400;
    text-transform: uppercase;
  }

  &__descr {
    margin: 0;
    margin-bottom: 40px;
    font-family: TT Norms Pro;
    font-size: 24px;
    font-weight: 300;
  }
}
