.link-list {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-auto-flow: row dense;
  align-items: center;
  gap: 20px;

    &__item {
  grid-column: 6 span;
  grid-auto-flow: row dense;

    }

    &__link svg path {
      fill: var(--shamp-color);
    }
    &__link svg polygon {
      fill: var(--shamp-color);
    }
  }
