.footer {
  background-color: var(--dark-color);

  &__container {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    padding-top: 93px;
    padding-bottom: 73px;
    gap: 29px;
  }

  &__logo {
    grid-column: 3 span;
  }

  &__title {
    grid-column: 4/8 span;
    margin: 0;
    margin-bottom: 73px;
    color: var(--shamp-color);
    font-size: 24px;
    font-weight: 400;
  }

  &__list {

    &-info {
      grid-column: 6 span;


      &-item:not(:last-child) {
        margin-bottom: 16px;
      }

      &-text {
        margin: 0;
        color: var(--shamp-color);
        font-size: 16px;
        font-weight: 400;

        &--bold {
          font-weight: 700;
        }
      }
    }

    &-organs {
      grid-column: 6 span;

      &-item {
        display: flex;
        flex-direction: column;
      }

      &-item:not(:last-child) {
        margin-bottom: 16px;
      }

      &-text {
        margin-bottom: 5px;
        color: var(--shamp-color);
        font-size: 16px;
        font-weight: 700;
        line-height: 20px;
      }

      &-phone {
        color: var(--shamp-color);
        font-size: 16px;
        font-weight: 400;
        line-height: 20px;
      }
    }


  }
  &__bottom {
    display: flex;
    grid-column: 12 span;
  }

  &__list-social {
    margin-left: auto;
  }
}
