.reviews {

  &__container {
    // display: flex;
    // flex-wrap: wrap;
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    padding-top: 80px;
    padding-bottom: 80px;

  }

  background-color: var(--gray-color);

  &__title {
    grid-column: 12 span;
    margin: 0;
    margin-bottom: 50px;
    text-align: center;
    width: 100%;
    font-family: Kudryashev Headline;
    font-size: 80px;
    font-weight: 500;
    text-transform: uppercase;
  }

  &__rating {
    grid-column: 5 span;
    display: flex;
    flex-direction: column;
    justify-content: center;

    &-list {
      display: flex;
      justify-content: center;
    }

    &-item:not(:last-child) {
      margin-right: 10px;
    }
  }

  &__descr {
    margin: 0;
    text-align: center;
    align-self: center;
    width: 90%;
    font-family: TT Norms Pro;
    font-size: 32px;
    font-weight: 400;
    color: #363636;
  }

  &__amount {
    margin-bottom: 10px;
    text-align: center;
    font-size: 48px;
    font-weight: 400;
  }

  &__right {
    grid-column: 6/8 span;
  }
}


