.custom-check {
  padding-left: 22px;

  &__checkbox {
    position: absolute;
    margin-top: 3.5px;
    margin-left: -21.5px;
    width: 13px;
    height: 13px;
    border-radius: 30px;
    border: 2px solid var(--dark-color);
    // background-color: var(--dark-color);
    box-shadow: 0 0 0 1px var(--gray-color);
    transition: border-color .3s ease-in-out,
      opacity .3s ease-in-out,
      background-color .3s ease-in-out,
      box-shadow .3s ease-in-out;

    &:before {
      content: '';
      position: absolute;
      top: 1px;
      left: 1px;
      width: 7px;
      height: 7px;
      border-radius: 30px;
      background-color: var(--dark-color);
      transform: rotate(42deg);
      opacity: 0;
    }

    // &::after {
    //   content: '';
    //   position: absolute;
    //   top: 5px;
    //   left: 3px;
    //   width: 2px;
    //   height: 5px;
    //   background-color: var(--light-color);
    //   transform: rotate(-42deg);
    //   opacity: 0;
    // }
  }

  &__input {
    position: absolute;
    width: 1px;
    height: 1px;
    overflow: hidden;
    clip: rect(0 0 0 0);

    // checked
    &:checked+.custom-check__checkbox {
      background-color: var(--primary-color);
      box-shadow: 0 0 0 1px var(--primary-color);
    }

    &:checked+.custom-check__checkbox:after,
    &:checked+.custom-check__checkbox:before {
      opacity: 1;
    }

    // focused
    &:focus-visible+.custom-check__checkbox {
      box-shadow:
        0 0 0 1px var(--grey-color),
        0 0 0 2px var(--primary-color);
    }

    &:checked:focus-visible+.custom-check__checkbox {
      box-shadow:
        0 0 0 1px var(--primary-color),
        0 0 0 2px var(--primary-color);
    }

    // disabled
    &:disabled+.custom-check__checkbox {
      box-shadow: 0 0 0 1px var(--grey-color);
    }

    &:checked:disabled+.custom-check__checkbox {
      box-shadow: 0 0 0 1px var(--grey-color);
      background-color: var(--grey-color);
    }
  }
}
