.form {
  display: flex;
  flex-wrap: wrap;



  &__label {
    margin-bottom: 18px;

    &:not(:nth-child(3)) {
      margin-right: 32px;
      width: 100%;
      max-width: 370px;
    }

    &:last-child {
      max-width: none;
    }
  }

  &__input {
    padding-top: 15px;
    padding-bottom: 15px;
    padding-left: 24px;
    padding-right: 15px;
    width: 100%;
    border-radius: 20px;
    border: 1px solid var(--dark-color);
    // background-color: var(--gray-color);
  background-color: #E8E5E0;
  }

  &__btn {
    margin-bottom: 18px;
    padding-left: 45px;
    padding-right: 45px;
  }

  &__link {
    color: var(--dark-color);
    border-bottom: 1px solid var(--dark-color);
  }
}

